*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --white-color: #fff;
  --black-color: #000;
  --green-bg-color: #84a178;
  --green-product-border-color: #96af8c;
  --white-product-modal-color: #d9d9d9;
  --green-product-modal-color: rgba(156, 190, 143, .7);
  --orange-product-modal-color: rgba(213, 117, 63, .7);
  --product-modal-shadow-color: rgba(212, 20, 67, .1);
  --light-green-products-bg-color: #aec6a5;
  --green-products-bg-color: #9cbe8f;
  --yellow-products-bg-color: #efd478;
  --orange-products-bg-color: #d6936d;
  --green-reviews-color: #84a178;
  --gallery-bg-color: #f5f5f5;
  --modal-form-input-color: rgba(248, 210, 78, .1);
  --white-content-color: #fcfcfc;
  --black-content-color: #131313;
  --yellow-content-color: #f8d24e;
  --green-content-color: #0a8817;
  --about-yellow-color: rgba(248, 210, 78, .7);
  --contacts-border-color: #e1e1e1;
  --contacts-color-text-time: #ababab;
  --contacts-bg-card-color: #fff;
  --footer-copyright-color: #7a7a7a;
  --footer-copyright-border: #e1e1e1;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --transition-dur-and-func-btns: 1000ms ease-in-out;
  --transition-modal-window: transform 1500ms ease;
  --animation: 250ms linear;
}

.products__name.milkshake:before, .products__name.coffee:before, .products__name.cream:before {
  background-position: center;
  background-size: cover;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  width: 100%;
  color: #131313;
  cursor: url("cursor-custom.d8582cc2.svg"), auto;
  background-color: #fcfcfc;
  background-image: url("fon-2.325d43d9.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  margin: 0;
  font-family: Montserrat, Arial, Helvetica, sans-serif;
  font-size: 12px;
  line-height: 1.86;
  top: 0 !important;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  cursor: url("cursor-pointer.4a8eb46e.svg"), auto;
  text-decoration: none;
}

button {
  cursor: url("cursor-pointer.4a8eb46e.svg"), auto;
  border: none;
}

h1, h2, h3, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

