.mobile-menu {
  z-index: 20;
  width: 100%;
  min-height: 100vh;
  background-color: #84a178;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.mobile-menu.is-open {
  transform: translateX(0);
}

.mob-menu-list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin-top: 80px;
  margin-bottom: 111px;
  display: flex;
}

.mob-list__link {
  color: #fcfcfc;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.21875;
}

.mob-list__link:hover, .mob-list__link:focus {
  color: #131313;
}

@media (min-width: 768px) {
  .mob-menu-list {
    align-items: flex-start;
    gap: 40px;
    margin-top: 100px;
  }

  .mob-list__link {
    color: #fcfcfc;
    font-size: 48px;
    line-height: 1.22917;
  }

  .mobile-menu.is-open {
    transform: translateX(-24%);
  }

  .mobile-btn {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .mobile-menu, .mobile-menu.is-open {
    display: none;
  }
}

.button {
  color: #131313;
  background-color: #f8d24e;
  border-radius: 10px;
  padding: 12px 34px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.28571;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.button__span {
  justify-content: center;
  align-items: center;
  gap: 8px;
  display: flex;
}

.button:hover, .button:focus {
  background-color: #0a8817;
}

.button:focus {
  color: #fcfcfc;
}

.button:disabled {
  background-color: #ababab;
}

.btn-arrow {
  width: 12px;
  height: 12px;
  fill: #131313;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.button:focus .btn-arrow, .button-contour:focus .btn-arrow {
  fill: #fcfcfc;
}

.button-yellow {
  color: var(--black-content-color);
  background-color: var(--yellow-content-color);
  transition: background-color 400ms var(--timing-function), color 400ms var(--timing-function);
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 12px 34px;
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
}

.button-yellow:hover, .button-yellow:focus {
  background-color: var(--green-content-color);
}

.button-yellow:focus {
  color: var(--white-content-color);
}

.button-border-yellow {
  border: 2px solid var(--yellow-content-color);
  color: var(--black-content-color);
  transition: border var(--transition-dur-and-func-btns), color var(--transition-dur-and-func-btns);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  outline: none;
  padding: 10px 46px;
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
}

.button-border-yellow:hover, .button-border-yellow:focus {
  border: 2px solid var(--green-content-color);
}

.button-border-yellow:focus {
  color: var(--white-content-color);
}

.button-icon {
  height: 12px;
  width: 12px;
  stroke: var(--black-content-color);
  transition: stroke var(--transition-dur-and-func-btns);
  margin-left: 6px;
}

.button-border-yellow:focus .button-icon, .button-yellow:focus .button-icon {
  stroke: var(--white-content-color);
}

.modal-hero {
  width: 100%;
  transition: var(--transition-modal-window);
  padding: 32px 40px;
  overflow-y: auto;
  transform: translate(-50%, -50%)scale(1);
}

.backdrop.is-hidden .modal-hero {
  transform: translate(-50%, -50%)scale(.5);
}

.modal-hero-form {
  flex-direction: column;
  display: flex;
}

.modal-hero-list {
  cursor: pointer;
  text-align: center;
  text-transform: uppercase;
  color: var(--black-content-color);
  justify-content: space-between;
  font-weight: 700;
  display: flex;
}

label.modal-hero-form__label, .modal-hero-form__label--bloc {
  width: 100%;
}

.modal-hero-form__title {
  text-align: center;
  color: var(--black-content-color);
  margin-bottom: 103px;
  font-family: Seymour One;
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
}

.modal-hero-list li {
  width: 132px;
  height: 135px;
  border-radius: 15.7808px;
  padding-top: 95px;
  padding-bottom: 20px;
  transition-duration: .5s;
  position: relative;
}

.modal-hero-form__ice-cream {
  border: 2px solid var(--green-product-border-color);
  filter: drop-shadow(0px 5.26027px 19.726px var(--product-modal-shadow-color) );
}

.modal-hero-form__ice-cream:focus, .modal-hero-form__ice-cream:hover {
  background: linear-gradient(0deg, var(--green-product-modal-color), var(--green-product-modal-color) ), var(--white-product-modal-color);
}

.modal-hero-form__ice-coffee {
  border: 2px solid var(--yellow-products-bg-color);
  box-shadow: 0px 5.26027px 19.726px var(--product-modal-shadow-color);
}

.modal-hero-form__ice-coffee:focus, .modal-hero-form__ice-coffee:hover {
  background: linear-gradient(0deg, var(--about-yellow-color), var(--about-yellow-color) ), var(--white-product-modal-color);
}

.modal-hero-form__ice-milkshakes {
  border: 2px solid var(--orange-products-bg-color);
  filter: drop-shadow(0px 5.26027px 19.726px var(--product-modal-shadow-color) );
}

.modal-hero-form__ice-milkshakes:focus, .modal-hero-form__ice-milkshakes:hover {
  background: linear-gradient(0deg, var(--orange-product-modal-color), var(--orange-product-modal-color) ), var(--white-product-modal-color);
}

.modal-hero-form__img {
  width: 130px;
  height: 130.6px;
  z-index: 3;
  background-position: center;
  background-size: cover;
  border-radius: 50%;
  position: absolute;
  top: -60%;
  left: 0;
}

.modal-hero-form__img--green {
  background-image: url("ice-cream-mobile.ed2a48a2.png");
}

.modal-hero-form__img--yellow {
  background-image: url("ice-coffee-mobile.854bc40d.png");
}

.modal-hero__img--red {
  background-image: url("milkshake-mobile.9436b347.png");
}

.modal-hero-form__circle {
  width: 130px;
  height: 130.6px;
  z-index: 2;
  border-radius: 50%;
  position: absolute;
  top: -50%;
  left: 0;
}

.modal-hero__circle--green {
  background: linear-gradient(0deg, rgba(156, 190, 143, .7), rgba(156, 190, 143, .7)), #d9d9d9;
  box-shadow: 0 5.26027px 19.726px rgba(10, 136, 23, .25);
}

.modal-hero-form__circle--yellow {
  background: linear-gradient(0deg, rgba(248, 210, 78, .7), rgba(248, 210, 78, .7)), #d9d9d9;
  box-shadow: 0 5.26027px 19.726px rgba(153, 114, 23, .25);
}

.modal-hero-form__circle--red {
  background: linear-gradient(0deg, rgba(213, 117, 63, .7), rgba(213, 117, 63, .7)), #d9d9d9;
  box-shadow: 0 5.26027px 19.726px rgba(0, 0, 0, .25);
}

.modal-hero-item {
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
}

.modal-hero-form__button {
  margin-left: auto;
  margin-right: auto;
}

