.contacts {
  padding-bottom: 40px;
}

.contacts__item {
  background-color: var(--white-content-color);
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  transform: translate(0px, 20%) scale(0.5);
  opacity: 0;
  transition: transform 0.8s ease 0s;
}

.contacts__item._active {
  transform: translate(0px, 0px) scale(1);
  opacity: 1;
}

.contacts__text {
  display: inline-block;
  background-color: var(--yellow-content-color);
  border-radius: 4px;
  padding-left: 24px;
  padding-right: 24px;
  font-weight: 500;
  @include font-contacts(12px, 22px);
  color: var(--black-content-color);
  margin-bottom: 20px;
}

.address {
  margin-bottom: 48px;
}

.address__text {
  font-weight: 500;
  @include font-contacts(12px, 22px);
  color: var(--black-content-color);
  margin-bottom: 8px;
}

.address__link {
  position: relative;
  font-weight: 500;
  @include font-contacts(12px, 22px);
  color: var(--orange-products-bg-color);
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: border-color 400ms var(--timing-function);

  &:hover,
  &:focus {
    color: var(--orange-products-bg-color);
    border-color: var(--orange-products-bg-color);
  }
}

.work-list {
  margin-bottom: 48px;
  position: relative;
}

.work-list__item:not(:last-child) {
  margin-bottom: 8px;
}

.work-list__day {
  font-weight: 500;
  @include font-contacts(12px, 22px);
  color: var(--black-content-color);

  &::after {
    content: '';
    position: absolute;
    width: 100%;
    border: 1px solid var(--contacts-border-color);
    background-color: var(--contacts-border-color);
    left: 0;
    top: -24px;
  }
}

.work-list__time {
  font-weight: 500;
  @include font-contacts(12px, 22px);
  color: var(--contacts-color-text-time);

  &::after {
    content: '';
    position: absolute;
    width: 100%;
    border: 1px solid var(--contacts-border-color);
    background-color: var(--contacts-border-color);
    left: 0;
    bottom: -24px;
  }
}

.contacts-page__info {
  font-weight: 500;
  @include font-contacts(12px, 18px);
  color: var(--black-content-color);
  text-decoration: none;
}

@media screen and (max-width: 767px) {
  .contacts__list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-bottom: 32px;
  }

  .contacts__item {
    padding: 40px 32px;
  }

  .contacts-btn-list {
    display: flex;
    flex-direction: column;
    gap: 16px;
    text-align: center;
  }

  .contacts-btn-list__button-locations,
  .contacts-btn-list__button-franchise {
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  .contacts__list {
    display: flex;
    gap: 16px;
    flex-wrap: wrap;
    margin-bottom: 60px;
  }

  .contacts__item {
    width: 224px;
    padding: 40px 24px;
    flex-basis: calc((100% - 32px) / 3);
  }

  .contacts__text {
    padding-left: 13px;
    padding-right: 13px;
    font-weight: 500;
    @include font-contacts(16px, 30px);
    margin-bottom: 32px;
  }

  .address {
    margin-bottom: 64px;
  }

  .address__text {
    @include font-contacts(14px, 26px);
    margin-bottom: 12px;
  }

  .address__link {
    @include font-contacts(14px, 26px);
  }

  .work-list {
    margin-bottom: 64px;
  }

  .work-list__item:not(:last-child) {
    margin-bottom: 12px;
  }

  .work-list__day {
    @include font-contacts(14px, 26px);

    &::after {
      top: -32px;
    }
  }

  .work-list__time {
    @include font-contacts(14px, 26px);

    &::after {
      bottom: -32px;
    }
  }

  .contacts-page__info {
    @include font-contacts(14px, 24px);
  }

  .contacts-btn-list {
    display: flex;
    justify-content: center;
    gap: 16px;
  }
}

@media screen and (min-width: 1200px) {
  .contacts {
    padding-bottom: 120px;
  }

  .contacts__list {
    gap: 32px;
    margin-bottom: 64px;
  }

  .contacts__item {
    width: 368px;
    padding: 40px 40px;
    background-color: var(--contacts-bg-card-color);
    flex-basis: calc((100% - 64px) / 3);
  }

  .address__text {
    @include font-contacts(16px, 30px);
    margin-bottom: 8px;
  }

  .address__link {
    @include font-contacts(16px, 30px);
  }

  .work-list__day {
    @include font-contacts(16px, 30px);
  }

  .work-list__time {
    @include font-contacts(16px, 30px);
  }

  .contacts-page__info {
    @include font-contacts(16px, 30px);
  }
}
