.section__about {
  background-color: var(--green-bg-color);
  padding-top: 60px;
  padding-bottom: 42px;
}

.about__container {
  width: 100%;
}

.section__label {
  @include fonts(18px, 700, 21px);

  text-align: center;
  text-transform: uppercase;
  color: var(--white-content-color);

  margin-bottom: 12px;
  transform: scale(0, 1);
  transition: transform 0.8s ease 0s;
}

.section__label._active {
  transform: scale(1, 1);
}

.section__title {
  display: block;
  margin-left: auto;
  margin-right: auto;

  font-family: 'seymour one';

  @include fonts(30px, 400, 38px);

  text-align: center;
  text-transform: uppercase;
  color: var(--white-content-color);

  width: 200px;
  margin-bottom: 32px;
  transform: translate(0px, 120%);
  opacity: 0;
  transition: transform 0.8s ease 300ms, opacity 0.8s ease 300ms;
}

.section__title._active {
  transform: translate(0px, 0px);
  opacity: 1;
}

.about__image-pad {
  position: relative;

  margin-left: auto;
  margin-right: auto;
  width: 278px;
  height: 278px;
  background-color: var(--about-yellow-color);
  border-radius: 50%;

  margin-bottom: 62px;
  transform: scale(0);
  transition: transform 0.8s ease 0ms;
}

.about__image-pad._active {
  transform: scale(1);
}

.about__picture {
  position: absolute;
  top: 22;
}

.about__description {
  width: 220px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 4px;

  @include fonts(12px, 400, 21px);

  text-align: center;
  color: var(--white-content-color);
  transform: translate(0px, 100%);
  opacity: 0;
  transition: transform 0.8s ease 0ms, opacity 0.8s ease 0ms;
}

.about__description._active {
  transform: translate(0px, 0px);
  opacity: 1;
}

.about__main-text {
  width: 220px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;

  @include fonts(12px, 400, 19px);

  text-align: center;
  color: var(--white-content-color);
  transform: translate(0px, 100%);
  opacity: 0;
  transition: transform 0.8s ease 0ms, opacity 0.8s ease 0ms;
}

.about__main-text._active {
  transform: translate(0px, 0px);
  opacity: 1;
}

.about__main-text--tablet {
  display: none;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 48px;

  @include fonts(12px, 400, 19px);

  text-align: center;
  color: var(--white-content-color);
  transform: translate(0px, 100%);
  opacity: 0;
  transition: transform 0.8s ease 0ms, opacity 0.8s ease 0ms;
}

.about__main-text--tablet._active {
  transform: translate(0px, 0px);
  opacity: 1;
}

.about__button {
  display: flex;
  align-items: center;
  margin: 0 auto;
}

@media screen and (min-width: 320px) {
  .about__container {
    width: 320px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .section__about {
    padding-bottom: 48px;
  }

  .about__container {
    width: 768px;
    padding-left: 2px;
    padding-right: 2px;
  }

  .section__label {
    @include fonts(22px, 800, 27px);
  }

  .section__title {
    @include fonts(34px, 400, 43px, 0.06em);
    min-width: 456px;
    margin-bottom: 72px;
  }

  .about__content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-basis: calc((100% - 32px) / 2);
    gap: 32px;
  }

  .about__image-pad {
    margin-left: 0;
    margin-right: 0;
    width: 334px;
    height: 330px;
  }

  .about__picture {
    width: 334px;
    height: 330px;
  }

  .about__description {
    text-align: left;
    margin-left: 0;
    margin-right: 0;
    width: 334px;
    margin-bottom: 8px;
  }

  .about__main-text {
    margin-left: 0;
    margin-right: 0;
    width: 334px;
    margin-bottom: 8px;
    text-align: left;
  }

  .about__main-text--tablet {
    display: block;
    margin-left: 0;
    margin-right: 0;
    width: 334px;
    text-align: left;
  }

  .about__button {
    margin-left: 0;
  }
}

@media screen and (min-width: 1200px) {
  .section__about {
    padding-bottom: 89px;
  }

  .about__container {
    width: 1200px;
    padding-left: 15px;
    padding-right: 15px;
  }

  .section__label {
    @include fonts(30px, 800, 37px);
  }

  .section__title {
    @include fonts(36px, 400, 45px, 0.06em);
    min-width: 490px;
    margin-bottom: 82px;
  }

  .about__content {
    justify-content: flex-start;
    gap: 100px;
  }

  .about__image-pad {
    width: 500px;
    height: 500px;
    margin-bottom: 0px;
  }

  .about__picture {
    top: 32;
    left: 7;
    width: 490px;
    height: 490px;
  }

  .about__description {
    width: 463px;
    @include fonts(16px, 400, 28px);
    text-align: left;
    margin-bottom: 8px;
  }

  .about__main-text {
    @include fonts(14px, 400, 27px);
    text-align: left;
    width: 463px;
    margin-bottom: 8px;
  }

  .about__main-text--tablet {
    @include fonts(14px, 400, 27px);
    text-align: left;
    width: 457px;
    margin-bottom: 48px;
  }

  .about__button {
    margin-left: 0;
  }
}
