.section__advantages {
  background-color: var(--green-bg-color);
  padding-bottom: 60px;
}

.advantages__container {
  width: 100%;
}

.advantages__item {
  &::before {
    content: '';
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 16px;
    width: 50px;
    height: 50px;
    background-repeat: no-repeat;
    background-color: var(--yellow-content-color);
    border-radius: 50%;
    background-position: 50% 50%;
  }

  &:not(:last-child) {
    margin-bottom: 22px;
  }

  transform: scale(-1, 0);
  transition: transform 0.8s ease 0ms;
}

.advantages__item._active {
  transform: scale(1, 1);
}

.advantages__item--cow {
  &::before {
    background-image: url('../images/advantages/cow.png');
  }
}

.advantages__item--weight {
  &::before {
    background-image: url('../images/advantages/weight.png');
  }
}

.advantages__item--ice-cream {
  &::before {
    background-image: url('../images/advantages/ice-cream.png');
  }
}

.advantages__value {
  font-family: 'Roboto';
  @include fonts(36px, 700, 42px);

  color: var(--white-content-color);
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 18px;
}

.advantages__description {
  font-family: 'Montserrat';
  display: block;
  margin-left: auto;
  margin-right: auto;
  @include fonts(12px, 700, 20px);
  color: var(--white-content-color);
  text-align: center;
  width: 230px;
}

@media screen and (min-width: 480px) {
  .advantages__container {
    width: 320px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .section__advantages {
    padding-bottom: 40px;
  }

  .advantages__container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .advantages__list {
    display: flex;
    justify-content: center;
    gap: 80px;
  }

  .advantages__item {
    flex-basis: calc((100% - 312px) / 3);

    &::before {
      margin-bottom: 32px;
    }

    &:not(:last-child) {
      margin-bottom: 0px;
    }
  }

  .advantages__value {
    font-family: 'Montserrat';
  }

  .advantages__description {
    width: 155px;
    @include fonts(14px, 400, 20px);
  }
}

@media screen and (min-width: 1200px) {
  .section__advantages {
    padding-bottom: 86px;
  }

  .advantages__container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }

  .advantages__list {
    justify-content: space-between;
    gap: 0px;
  }

  .advantages__item {
    flex-basis: calc((100% - 96px) / 3);

    &::before {
      margin-bottom: 40px;
    }
  }

  .advantages__value {
    font-family: 'Montserrat';
    @include fonts(58px, 700, 71px);
    margin-bottom: 16px;
    text-transform: lowercase;
  }

  .advantages__description {
    width: 368px;
    @include fonts(16px, 400, 28px, 0.02em);
  }
}
